@font-face {
    font-family: 'Roboto-Bold';
    src: local('Roboto'), url(./../../fonts/Roboto-Bold.ttf) format('truetype');
}
$tablet: 1023px; 
$mobile: 767px;

.navbar {
    display: flex;
    position: fixed;
    box-sizing: border-box;
    max-width: 100%;
    margin: 0px;
    align-items: center;
    background: #151B1F none repeat scroll 0% 0%;
    flex-direction: column;
    width: 64px;
    height: 100%;
    left: 0px;
    top: 0px;
    justify-content: flex-start;  
    z-index: 500; 
}
.navbar__language{
    position: absolute;
    width: 23px;
    height: 16px;
    left: 21px;
    top: 96%;
    font-family: Roboto-Bold;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 15px;
    color: #0EC261;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); 
}

@media screen and (max-width: $tablet) { 
    .navbar {
        width: 85.77px;
    }
} 
@media screen and (max-width: $mobile) { 
    .navbar {
        width: 33.57px;
        height: 33.45px;
        left: 16px;
        background: none;
    }
    .navbar__language{
        display: none;
    } 
}
